/* Page Title */
.pageTitle {
    font-size: 2rem;
    font-weight: bold;
    color: #ff6b81;
  }
  
  /* Form Styles */
  .formContainer {
    background-color: #f8f9fa;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Scoped Buttons */
  .formContainer .submitButton {
    background-color: #28a745;
    color: white;
    padding: 10px 20px;
    font-size: 1rem;
    border-radius: 5px;
    border: none;
  }
  
  .formContainer .submitButton:hover {
    background-color: #218838;
  }
  
  .formContainer .resetButton {
    background-color: #ff6b81;
    color: white;
    padding: 10px 20px;
    font-size: 1rem;
    border-radius: 5px;
    border: none;
  }
  
  .formContainer .resetButton:hover {
    background-color: #ff416c;
  }
  